.loader {
  width: 48px;
  height: 48px;
  background: radial-gradient(#ff3d00 0%, #ff3d00 14%, #fff 15%, #fff 100%) center / cover;
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  height: 16px;
  width: 4px;
  transform-origin: 25% 0;
  box-sizing: border-box;
  background: #ff3d00;
  animation: rotation 10s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)rotate(0deg);
}

.loader:before {
  height: 22px;
  width: 2px;
  animation-duration: 1s;
  transform: translateX(-50%)rotate(0deg);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  gap: 20px;
  margin-top: 30px;
  display: flex;
}

span {
  font-weight: bold;
}

.descr {
  width: 500px;
}

h2 {
  margin-left: 20px;
}

.error {
